@import '~bulma/bulma.sass';

$main: #FF851B;
$font-color: #4a4a4a;
$decline-color: #ef224b;
$logo-font: 'Cinzel', serif;
$slogan: 'Dosis', sans-serif;
$article-font: BlinkMacSystemFont;
$article-text-font: Roboto, sans-serif;
// #e1b382

html {
  background: #f0f4f8;
}

a {
  color: $main;
}

.home-container {
  background-image: url('./images/background.jpg');
  background-position: center;
  background-size: cover;
  height: 100vh;
}

.register-container {
  display: flex;
  justify-content: center;
  width: 100vw;
  align-items: center;
  margin-top: -40px;
}

.radio {
  display: inline-block;

}

.register-forms-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border: 1px solid #dbdbdb;
  padding: 20px 20px;
  border-radius: 4px;
  box-shadow: inset 0 0.0625em 0.125em rgba(10, 10, 10, 0.05);
}

.register-forms-container:hover {
  box-shadow: inset 0 0.0625em 0.125em rgba(10, 10, 10, 0.25);
}

.register-forms {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

.category {
  flex-direction: row;
  justify-content: space-between;
}

.category label {
  margin-right: 20px;
}

.register-type {
  margin-left: 40px;
}

.register-forms-span {
  display: inline-block;
  margin: 30px 0 0 10px;
}

.register-section {
  margin-top: 10px;
  padding: 0 20px;
}

.register-section.add-training .button {
  margin: 40px 0 0 40%;
  display: inline-block;
}

.register-section .input {
  width: 360px;
}

.field {
  margin-right: 40px;
}

.input-container {
  display: flex;
  justify-content: space-between;
}

.select,
.is-multiple {
  margin-top: 5px;
}

.profile-choices-container {
  display: flex;
  justify-content: space-evenly;

}

.index {
  margin-bottom: 30px;
}

.profile-choices {
  width: 200px;
  height: 200px;
  // border: 2px solid black;
  padding: 20px;
  margin: 30px;
  text-align: center;
  cursor: pointer;
  font-size: 25px;
}

.small-profile-choices {
  width: 150px;
  cursor: pointer;
  text-align: center;
  border-bottom: 1px solid #f0f4f8;
}

.small-profile-choices:hover {
  color: $main;
  border-bottom: 1px solid $main;
}

.card-header-title {
  display: inline-block;
}

.card {
  padding: 10px;
}

.done {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 50px;
}

.done-booking {
  padding-bottom: 20px;
  width: 620px;
  border-bottom: 3px solid $main;
}

.done .subtitle {
  text-align: center;
}

.done-subtitle-wrap {
  margin-top: -140px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.selected-menu-choice {
  color: $main;
}

.add-portfolio {
  position: fixed;
  top: 0px;
  left: 48%;
  animation: heartbeat 4s infinite;
  z-index: 2;
}

@keyframes heartbeat {
  0% {
    transform: scale(1);
  }

  20% {
    transform: scale(0.9);
  }

  50% {
    transform: scale(1);
  }

  80% {
    transform: scale(0.9);
  }

  100% {
    transform: scale(1);
  }
}

.portfolio-container {
  padding: 0 140px;
}

.images-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;

}

.single {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;

  video {
    width: 100%;
  }

  .small-image {
    width: 40%;
  }
}

.small-image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 50%;
  margin: 30px;

  .small-image-header {
    background: white;
    display: flex;
    align-items: center;

    .small-image-profile {
      display: flex;
      align-items: center;
      margin-left: 10px;
    }

    img {
      width: 41px;
      height: 41px;
      border: 1px solid $main;
      border-radius: 50%;
      margin: 6px;
    }
  }

  .small-image-footer {
    background: white;
    display: flex;
    flex-direction: column;
    padding: 15px;
    max-height: 350px;
    overflow: auto;

    .profile-footer {
      display: flex;
      flex-direction: column;
      align-items: start;
      margin: 0 0 15px 0;

      img {
        width: 10%;
        height: 10%;
        border: 1px solid $main;
        border-radius: 50%;
        margin-right: 3px;
      }

      .username {
        font-weight: bold;
        margin-right: 10px;
        color: $font-color;
      }
    }

    .like-counter {
      margin-left: 10px;
      cursor: pointer;
    }

    .username-footer {
      font-weight: bold;
      color: $font-color;
      margin-right: 7px;
    }

    .profile-footer-container {
      display: flex;
    }

    // .usernmae-container{
    //   display: flex;
    //   align-items: start;
    // }
  }
}

.article-container {
  display: flex;
  justify-content: space-around;
  align-items: stretch;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100vw;
  padding: 0 40px;
}

.card-article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 600px;
  margin-bottom: 80px;
  color: $font-color;

}


.article-title {
  padding: 0 10px;
  font-family: $article-font;
  margin: 10px 0;
  font-size: 30px;
  line-height: 1;
  text-align: center;
}

.article-text a {
  color: $font-color;
}

.article-author {
  display: inline-block;
  width: 100%;
  text-align: left;
  padding: 8px 0 0 57px;
}

.article-author a {
  color: $main;
}

.article-border {
  border-bottom: 2px solid $font-color;
  width: 90%;
