// Chats
.chat-profile-container {
  display: flex;
  padding: 53px 0px 0px 40px;
}

.profile-header-chat-page {
  display: flex;
  justify-content: center;
  margin: 5px 15px 5px 0px;
  align-items: center;
  text-align: left;
  border-bottom: 1px solid $font-color;
  width: 150px;
  cursor: pointer;
  padding-bottom: 15px;
}

.profile-header-chat-page a {
  color: $font-color;
}

.chat-profile-side {
  display: flex;
  flex-direction: column;
  padding: 30px;
  border-right: 1px solid $font-color;
  margin-right: 60px;
  height: 85vh;
  overflow: auto;
}

.messages-section {
  width: 70%;
  height: 85vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.profile-messages {
  border-bottom: 1px solid $font-color;
  padding-bottom: 15px;
  margin-top: 10px;
}

.messages-section form {
  display: flex;
  width: 400px;
}

.messages-form-container {
  display: flex;
  justify-content: center;
}

.chat-history {
  width: 93%;
  height: 400px;
  overflow: auto;
  transform: rotate(180deg);
}

.one-message-container {
  transform: rotate(180deg);
  margin: 15px;
  border-radius: 4%;
  display: flex;
  justify-content: right;
}

.one-message-container-other {
  transform: rotate(180deg);
  margin: 15px;
  border-radius: 4%;
  display: flex;
  justify-content: left;
}

.one-message {
  display: inline-block;
  background: $main;
  text-align: center;
  padding: 10px 18px;
  max-width: 50%;
  font-family: $article-text-font;
}

.one-message-other {
  display: inline-block;
  background: $font-color;
  text-align: center;
  padding: 10px 18px;
  max-width: 50%;
  font-family: $article-text-font;
  color: white;
}

.chat-history-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.no-messages {
  margin-top: 100px;
}

